.App {
  text-align: center;
}



.logo-pirates-heard {
  height: 40vmin;
  pointer-events: none;
  position: absolute;
  z-index: 1;
}

.logo-pirates-heard-2 {
  height: 40vmin;
  pointer-events: none;
  /*superpones la imagen 2 sobre la primera */
  position: relative;
  z-index: 1;
}

/* .contenidoImage{
  position: static;
} */


@media (prefers-reduced-motion: no-preference) {
  .logo-pirates-heard {
    animation: logo-pirates-heard-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@keyframes logo-pirates-heard-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}